import { h, Component } from 'preact'
import { Link } from 'preact-router/match'
import Button from '../../components/button'
import Anchor from '../../components/anchor'
import style from './style.css'

export default class Home extends Component {
  componentDidMount() {
    let index1 = 0
    let index2 = 0
    const text1 = 'Hello, World!'
    const text2 = 'I am Chris and I love building web applications!'
    const refreshRate = 60

    const tick = () => {
      if (!this.actionButtons) return
      this.actionButtons.style.opacity = 0
      if (index1 < text1.length) {
        this.greeting1.innerHTML += text1.charAt(index1)
        index1 += 1
        setTimeout(tick, refreshRate)
      } else if (index2 < text2.length) {
        this.greeting2.innerHTML += text2.charAt(index2)
        index2 += 1
        setTimeout(tick, refreshRate)
      } else {
        this.actionButtons.style.opacity = 1
      }
    }
    tick()
  }

  render() {
    return (
      <div className={style.home}>
        <h2 ref={(element) => { this.greeting = element }}></h2>
        <h2>
          <div ref={(element) => { this.greeting1 = element }}></div>
          <div ref={(element) => { this.greeting2 = element }}></div>
        </h2>
        <div className={style.homeActions} ref={(element) => { this.actionButtons = element }}>
          <Link href="/portfolio"><Button>View Portfolio</Button></Link>
          <Anchor href="/assets/documents/resume.pdf"><Button>View Resume</Button></Anchor>
        </div>
      </div>
    )
  }
}



